import React from 'react';
import './legend.scss';

const Legend = (props) => {
    let { legend, isSmallerFont } = props
    let legendDisplay = legend;

    return (
      <div className={'legend-chart clearfix'}>
        <ul>
          {
            legendDisplay.sort((first, second) => (first._id - second._id)).map((legend, i) => {
              return legend ? 
                  <li key={i}>
                    <span className={legend.value_name === "median_value" ? "legend-line" : "legend-color" } style={{ backgroundColor: legend.color }}>&nbsp;</span>
                    <span className={isSmallerFont ? "legend-name smaller-font":"legend-name"}>{legend.name}</span>
                  </li>
                : null;
            })
          }
        </ul>
      </div>
    );
}

export default Legend;
