import numbro from 'numbro';

export const convertTokpiOverallTotal = (revenue) => {
  let rev = numbro(revenue).format({thousandSeparated: false});

  if (isNaN(rev) === false && (rev <= -100000 || rev >= 100000)) {
    let divider = (rev < -10000000 || rev > 10000000) ? 1 : (rev > -1000000 && rev < 1000000) ? 100 : 10;
    let multiplier = (rev < -10000000 || rev > 10000000) ? 100 : (rev > -1000000 && rev < 1000000) ? 100 : 10;
    let chartSetup =  ((rev >= -1000000  && rev <= -10000000) || (rev >= 1000000  && rev <= 10000000)) ? {thousandSeparated: true, mantissa: 1} : {thousandSeparated: true};

    return `${rev < 0 ? `-`: ''}$${numbro(Math.round(Math.abs(rev / 1.0e+6 ) * multiplier) / divider).format(chartSetup)}M`;
  } else if (isNaN(rev) === false && rev !== 0) {
    return `${rev < 0 ? `-`: ''}$${numbro(Math.abs(Math.round(rev))).format({thousandSeparated: true})}`;
  } else {
    return '-';
  }
}; 

