export const convertToAvgNumber = (revenue) => {
  let rev = numbro(revenue).format({thousandSeparated: false});

  if (isNaN(rev) === false && (rev <= -100000 || rev >= 100000)) {
    return `${numbro(Math.round((rev / 1.0e+6 ) * 100) / 100).format({thousandSeparated: true})}M`;
  } else if (isNaN(rev) === false && (rev <= -1000 || rev >= 1000)) {
    return `${numbro(rev / 1.0e+3).format({thousandSeparated: true})}K`;
  } else if (isNaN(rev) === false && rev !== 0) {
    return numbro(Math.round(rev)).format({thousandSeparated: true});
  } else {
    return '-';
  }
};

export const convertToM = (revenue) => {
  let rev = numbro(revenue).format({thousandSeparated: false});

  if (isNaN(rev) === false && (rev <= -100000 || rev >= 100000)) {
    let output = numbro(Math.round((rev / 1.0e+6 ) * 100) / 100);
    return `${output.format({thousandSeparated: true })}M`;
  } else if (isNaN(rev) === false && rev !== 0) {
    return `${numbro(Math.round(rev)).format({thousandSeparated: true})}`;
  } else {
    return '-';
  }
};

export const convertToUSDGrowth = (revenue) => {
  let value = isNaN(revenue) === true ? Math.round(parseFloat(revenue.replace(/,/g, ''))) :  Math.round(parseFloat(revenue));

  if (isNaN(value) === false && value !== 0) {
    return `${value < 0 ? `-`: ''}$${numbro(Math.abs(value)).format({thousandSeparated: true})}`;
  } else {
    return '-';
  }
};

export const convertToPPvalue = (pp_value) => {
  let value = numbro(Math.abs(pp_value)).format({ mantissa: 1 });
  return isNaN(value) === false ? `${value}pp` : '0.0pp';
};

export const convertToPercent = (percent) => {
  let value = numbro(Math.abs(percent)).format({ mantissa: 1 });
  return isNaN(value) === false ? `${value}%` : '0.0%';
};

export const convertToPercentage = (percent) => {
  let value = Math.round(parseFloat(percent));
  return isNaN(value) === false ? value : '0';
};

export const convertToWholePercent = (percent) => {
  let value = numbro(parseFloat(percent)).format({ mantissa: 1 });
  return isNaN(value) === false ? value : '0.0';
};

export const convertToCount = (count) => {
  return isNaN(count) === false ? numbro(count).format({ thousandSeparated: true }) : '0';
};

export const roundUpToTenth = (value) => {
    let parsedValue = Math.round(parseFloat(value) * 10) / 10;
    return isNaN(parsedValue) === false ? parsedValue : 0;
};
