import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import { convertToPercentage } from '../../constants/formatter';

am4core.useTheme(am4themes_animated);


const LineChart = (props) => {
  const { 
          id, 
          data, 
          legend, 
          hideLabel, 
          hideValueLabel,
          isCardSmaller,
          isPercentage,
          isOneHundredPercent,
          withLabel,
          withDottedLine,
          withGridLines,
          dasharrayValues,
          isAnimationEnabled,
          isExecCardSmaller
        } = props;

  am4core.options.autoSetClassName = true;

  useLayoutEffect(() => {
    let lastCategory = data[data.length - 1].category;
    let chartPaddingRight = isCardSmaller === true && withLabel === true ? 12 : 8;
    let labelFontSize = isExecCardSmaller ? "0.6rem" :"0.8rem";
    let chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    
    chart.paddingRight = chartPaddingRight;
    chart.paddingLeft = 0;
    chart.paddingTop = 50;
    chart.paddingBottom = 0;
    chart.data = data;

    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    let categoryMinGridDistance = isCardSmaller ? 30 : 50;
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.renderer.minGridDistance = categoryMinGridDistance;
    categoryAxis.renderer.paddingTop = 5;
    categoryAxis.renderer.paddingBottom = -8;
    categoryAxis.renderer.labels.template.adapter.add("text", label => {
      return`[font-size: ${labelFontSize}]${label}[/]`;
    });
    categoryAxis.id = "myCategoryAxis";

    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    let valueAxisMinGridDistance = isOneHundredPercent && isCardSmaller ? 30 : 50;
    valueAxis.id = "myValueAxis";
    valueAxis.renderer.paddingLeft = -8;
    valueAxis.renderer.paddingRight = -8;
    valueAxis.renderer.minGridDistance = valueAxisMinGridDistance;
    valueAxis.renderer.labels.template.adapter.add("text", (text, label) => {
      let labelText = `${label.dataItem.value}${isPercentage ? "%" : ""}`;
      return labelText;
    });
    
    if (isOneHundredPercent === true) {
      valueAxis.min = 0;
      valueAxis.max = 100;
      valueAxis.strictMinMax = true;
    } else {
      valueAxis.extraMin = 0.2;
      valueAxis.extraMax = 0.2;
    }

    if (hideLabel) {
      chart.paddingTop = 0;
      chart.marginTop = 0;
      chart.valign = 'top';
      chart.contentValign = 'top';

      categoryAxis.renderer.grid.template.disabled = true;
      categoryAxis.renderer.labels.template.disabled = true;

      valueAxis.renderer.grid.template.disabled = true;
      valueAxis.renderer.labels.template.disabled = true;       
    } 

    if (hideValueLabel) {
      let chartPaddingTop = isCardSmaller && withDottedLine ? 15 : 10;
      chart.paddingTop = chartPaddingTop;
      chart.marginTop = 0;
      chart.valign = 'top';
      chart.contentValign = 'top';
      chart.strokeOpacity = 0;

      valueAxis.renderer.labels.template.disabled = true;      
    }
    
    if (withGridLines) {
      valueAxis.renderer.grid.template.strokeWidth = 1;
    } else if (isOneHundredPercent && withDottedLine) {
      valueAxis.renderer.grid.template.adapter.add("strokeWidth", (label, target, key) => {
        return  target.dataItem._text === "100" ? 1 : 0;
      });
      valueAxis.renderer.grid.template.strokeDasharray = dasharrayValues || "3,3";
    } else {
      valueAxis.renderer.grid.template.disabled = true;
      valueAxis.renderer.grid.template.strokeWidth = 0;
    }

    function createSeries(field, name, color, arrLength) {
      let series = chart.series.push(new am4charts.LineSeries());
      series.dataFields.valueY = field;
      series.dataFields.categoryX = "category";
      series.name = name;
      series.strokeWidth = 2;
      series.stroke = am4core.color(color);
      series.hiddenState.transitionDuration = 0;
      series.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;
      series.bulletsContainer.parent = chart.seriesContainer;

      let bullet = series.bullets.push(new am4charts.CircleBullet());
      let tooltipText = "[#fff]{name} in {categoryX}: [/][#fff font-weight: bold]{valueY.formatNumber('#.0')}%[/] [#fff]{additional}[/]";
      bullet.fill = am4core.color(color);
      bullet.tooltipText = tooltipText;

      if (withLabel) {
        let labelBullet = series.bullets.push(new am4charts.LabelBullet());
        let labelDX = isExecCardSmaller ? 21 : isCardSmaller ? 23: 25;
        labelBullet.label.hideOversized = false;
        labelBullet.label.truncate = false;
        labelBullet.label.verticalCenter = "middle";
        labelBullet.label.textAlign = "left";
        labelBullet.label.dx = labelDX;
        labelBullet.label.adapter.add("text", (label, target, key) => {
          let labelText = `${convertToPercentage(target.dataItem.values.valueY.value)}${isPercentage ? "%" : ""}`;
          return target.dataItem.categories.categoryX === lastCategory ? `[font-size: ${labelFontSize}]${labelText}[/]` : "";
        });
      }

      let circle = bullet.createChild(am4core.Circle);
      circle.radius = 4;
      circle.strokeWidth = 1;
      circle.fill = am4core.color("#fff");

      let segment = series.segments.template;
      segment.interactionsEnabled = true;

      let hs = segment.states.create("hover");
      hs.properties.strokeWidth = 5;

      return series;
    }

    for (let i = 0; i < legend.length; i++) {
      createSeries(legend[i].value_name, legend[i].name, legend[i].color, legend.length);
    }

    return () => {
      chart.dispose();
    };
  }, [
      id, 
      data, 
      legend, 
      hideLabel, 
      hideValueLabel,
      isCardSmaller,
      isPercentage,
      isOneHundredPercent,
      withLabel,
      withDottedLine,
      withGridLines,
      dasharrayValues,
      isAnimationEnabled,
      isExecCardSmaller
    ]
  );

  return (
    <div className="line-chart" id={id}></div>
  );
}

export default LineChart;