import React, { useState, forwardRef, Children } from 'react';
import { Dropdown, Form, Button, FormControl } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const SearchCheckboxDD = ({
  id,
  title,
  options,
  checkVal,
  setCheckVal,
  onApply,
  onCancel,
  valueApplied,
  visibleInModal,
  className,
}) => {
  const [show, setShow] = useState(false);
  const [searchVal, setSearchVal] = useState('');

  const onChangeCheck = (e) => {
    const checkValue = checkVal.some(item => item === e);
    const remove = checkVal.filter(item => item !== e);
    if (!checkValue) {
      setCheckVal([...checkVal, e])
    } else {
      setCheckVal(remove)
    }
  }

  const handleToggle = () => {
    setShow(!show);
    onCancel();
    setSearchVal('')
  }

  const CustomToggle = forwardRef(({ children, onClick }, ref) => (
    <Button
      className={`dropdown-toggle ${checkVal.length !== 0 ? 'hasPick' : ''}`}
      variant='light'
      ref={ref}
      onClick={(e) => {
        e.preventDefault();
        onClick(e);
      }}>
      {children}
    </Button>
  ));

  const CustomMenu = forwardRef(({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {
    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <FormControl
          autoFocus
          className="my-2 w-auto"
          placeholder={`Search for ${title}`}
          onChange={(e) => setSearchVal(e.target.value)}
          value={searchVal}
        />
        <ul className="list-of-checkbox custom-scroll">
          {Children.toArray(children).filter(
            (child) => !searchVal || child.props.label.toLowerCase().includes(searchVal.toLowerCase())
          )}
        </ul>
        <div className="btn-block">
          <Button variant="link" onClick={handleToggle}>Cancel</Button>
          <Button variant="primary" onClick={() => { onApply(); setShow(!show); }}>Apply</Button>
        </div>
      </div>
    );
  },
  );
  return (
    <>
      {visibleInModal
        ? <div className={`checkbox-in-modal narrow-list filter-modal ${className ? className : ''}`}>
          <div className="title">{title}</div>
          <div>
            <FormControl
              className="mb-2"
              placeholder={`Search for ${title}`}
              onChange={(e) => setSearchVal(e.target.value)}
              value={searchVal}
            />
            <ul className="list-of-checkbox custom-scroll">
              <Form>
                {options.map(option => (
                  <Form.Check
                    key={option.key}
                    custom
                    type='checkbox'
                    id={`search_check_modal-${id}-${option.key}`}
                    label={option.text}
                    checked={checkVal.some(item => item === option.key)}
                    onChange={() => onChangeCheck(option.key)}
                  />
                )).filter(
                  (child) => !searchVal || child.props.label.toLowerCase().includes(searchVal.toLowerCase())
                )}
              </Form>
            </ul>
          </div>
        </div>
        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`search-checkbox-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle as={CustomToggle} id={`search_check_dd-${id}`}>
            {valueApplied !== undefined
              ? valueApplied.length === 1
                ? valueApplied[0]
                : title + (valueApplied.length >= 1 ? ` ( ${valueApplied.length} )` : '')
              : title
            }
            <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>

          <Dropdown.Menu show={show} as={CustomMenu}>
            {options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`search_check-${id}-${option.key}`}
                label={option.text}
                checked={checkVal.some(item => item === option.key)}
                onChange={() => onChangeCheck(option.key)}
              />
            ))}
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default SearchCheckboxDD;
