import React, { useState } from 'react';
import { Dropdown, Form, Button, FormControl } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const ShowRankingDD = ({ className, id, visibleInModal, rankingVal, setRankingVal, onApply, onCancel }) => {
  const [show, setShow] = useState(false);

  const onChangeRadio = (e) => {
    setRankingVal({
      ...rankingVal,
      [e.target.name]: e.target.id
    })
  }

  const onChangeInput = (e) => {
    setRankingVal({
      ...rankingVal,
      numberInput: e.target.value >= 0 ? e.target.value : 0,
    })
  }

  const handleToggle = () => {
    setShow(!show)
    onCancel();
  }

  const content = <>
    <Form.Check
      custom
      type='radio'
      name='topBottom'
      label='Top'
      id='radio_Top'
      checked={rankingVal.topBottom === 'radio_Top'}
      onChange={(e) => onChangeRadio(e)}
    />
    <Form.Check
      custom
      type='radio'
      name='topBottom'
      label='Bottom'
      id='radio_Bottom'
      checked={rankingVal.topBottom === 'radio_Bottom'}
      onChange={(e) => onChangeRadio(e)}
    />

    <FormControl
      type="number"
      min="0"
      placeholder="0"
      name="numberInput"
      value={rankingVal.numberInput}
      onChange={onChangeInput}
    />

    <Form.Check
      custom
      type='radio'
      name='rankingPercentile'
      label='Ranking'
      id='radio_Ranking'
      checked={rankingVal.rankingPercentile === 'radio_Ranking'}
      onChange={(e) => onChangeRadio(e)}
    />
    <Form.Check
      custom
      type='radio'
      name='rankingPercentile'
      label='Percentile'
      id='radio_Percentile'
      checked={rankingVal.rankingPercentile === 'radio_Percentile'}
      onChange={(e) => onChangeRadio(e)}
    />
  </>


  let getLastDigit = (str, num) => {
    return num.toString();
  };
  let number_arr = [...getLastDigit`${rankingVal.numberInput}`];
  let last_digit = +number_arr.pop();
  let second_last_digit = +number_arr[number_arr.length - 1];
  let first = last_digit === 1;
  let second = last_digit === 2;
  let third = last_digit === 3;
  let substr;

  if (first && second_last_digit !== 1) {
    substr = 'st'
  } else if (second && second_last_digit !== 1) {
    substr = 'nd'
  } else if (third && second_last_digit !== 1) {
    substr = 'rd'
  } else {
    substr = 'th'
  }

  return (
    <>
      {visibleInModal
        ? <div className={`show-top-bottom-dd narrow-list filter-modal ${className ? className : ''}`}>
          <div className="title">Show Ranking</div>
          {content}
        </div>
        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`show-top-bottom-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle id={`radio_dd-${id}`} variant='light' className={rankingVal.numberInput !== 0 ? 'hasPick' : ''}>
            {rankingVal.topBottom || rankingVal.rankingPercentile || rankingVal.numberInput
              ? `${rankingVal.topBottom.split('_')[1]} 
                  ${rankingVal.numberInput}${rankingVal.rankingPercentile === 'radio_Percentile' ? substr : ''}
                  ${rankingVal.rankingPercentile.split('_')[1]}`
              : 'Show Ranking'
            }
            <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>

          <Dropdown.Menu show={show}>
            {content}
            <div className="btn-block">
              <Button variant="link" onClick={handleToggle}>Cancel</Button>
              <Button variant="primary" onClick={() => {
                onApply();
                setShow(!show);
              }}>Apply</Button>
            </div>
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default ShowRankingDD;
