import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const GaugeChart = (props) => {
  const { id, data } = props;

  let initialHandValue = "0.0";
  let initialColor = "#39484c";
  let axisLabelColor = "#A6BCD0";
  let axisColor = "#748A9D";

  const createRange = (axis2, item) => {
    let range = axis2.axisRanges.create();
    range.value = item.start_value;
    range.endValue = item.end_value;
    range.axisFill.fillOpacity = 1;
    range.axisFill.fill = item.color;
    range.axisFill.zIndex = -1;
    range.label.inside = true;
    range.label.location = 0.5;
    range.axisFill.tooltip = new am4core.Tooltip();
    range.axisFill.tooltipX = am4core.percent(50);
    range.axisFill.tooltipY = am4core.percent(25);
    range.axisFill.tooltipText = `[#fff bold]Range (${item.value}%)[/]\n[#fff]${item.start_value}% - ${item.end_value}%[/]`;
    range.axisFill.interactionsEnabled = true;
    range.axisFill.isMeasured = true;
  }

  const findItem = (handValue, legend) => {
    return handValue > 0 ? legend.find(item => handValue <= item.end_value) : null;
  }

  am4core.options.autoSetClassName = true;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.GaugeChart);
    chart.innerRadius = am4core.percent(80);
    chart.responsive.enabled = true;
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 0;
    chart.paddingBottom = 0;
    chart.marginRight = 0;
    chart.marginLeft = 0;
    chart.marginTop = 0;
    chart.marginBottom = 0;

    let axis = chart.xAxes.push(new am4charts.ValueAxis());
    axis.min = 0;
    axis.max = 100;
    axis.strictMinMax = true;
    axis.renderer.radius = am4core.percent(75);
    axis.renderer.inside = true;
    axis.renderer.line.strokeOpacity = 0.5;
    axis.renderer.line.stroke = am4core.color(axisColor);
    axis.renderer.ticks.template.disabled = false
    axis.renderer.ticks.template.strokeOpacity = 0.5;
    axis.renderer.ticks.template.stroke = am4core.color(axisColor);
    // axis.renderer.ticks.template.length = data.default_sizes.tick_length;
    axis.renderer.grid.template.disabled = true;
    axis.renderer.labels.template.fontSize = '0.5rem';
    axis.renderer.labels.template.fontWeight = "300";
    axis.renderer.labels.template.strokeOpacity = 0.5;
    axis.renderer.labels.template.stroke = am4core.color(axisLabelColor);

    let axis2 = chart.xAxes.push(new am4charts.ValueAxis());
    axis2.min = 0;
    axis2.max = 100;
    axis2.strictMinMax = true;
    axis2.renderer.labels.template.disabled = true;
    axis2.renderer.ticks.template.disabled = true;
    axis2.renderer.grid.template.disabled = true;

    for (let i = 0; i < data.legend.length; i++) {
      createRange(axis2, data.legend[i]);
    }

    let label = chart.radarContainer.createChild(am4core.Label);
    label.isMeasured = false;
    label.fontSize = '1rem';
    label.x = am4core.percent(70);
    label.y = am4core.percent(10);
    label.dy = 5
    label.horizontalCenter = "middle";
    label.verticalCenter = "bottom";
    label.text = initialHandValue + "%";
    label.fill = am4core.color(initialColor);

    // let label2 = chart.radarContainer.createChild(am4core.Label);
    // label2.isMeasured = false;
    // label2.fontSize = '0.7rem';
    // label2.dy = 20;
    // label2.horizontalCenter = "middle";
    // label2.verticalCenter = "bottom";
    // label2.fill = am4core.color(initialColor);

    let hand = chart.hands.push(new am4charts.ClockHand());
    hand.axis = axis2;
    hand.innerRadius = am4core.percent(25);
    hand.startWidth = 4;
    hand.pin.disabled = true;
    hand.value = parseFloat(initialHandValue);
    hand.fill = am4core.color(initialColor);
    hand.stroke = am4core.color(initialColor);

    hand.events.on("propertychanged", function (ev) {
      axis2.axisRanges.values[0].endValue = ev.target.value;
      axis2.axisRanges.values[1].value = ev.target.value;
      axis2.invalidate();
    });

    hand.events.on("positionchanged", function (ev) {
      let handValue = axis.positionToValue(hand.currentPosition);
      let percentageValue = axis2.positionToValue(hand.currentPosition);
      let item = findItem(handValue, data.legend);

      label.text = item ? percentageValue.toFixed(1) + "%" : initialHandValue + "%";
      // label2.text = data.overall.segment;
    });


    setTimeout(() => {
      new am4core.Animation(hand, {
        property: "value",
        from: data.overall.start_value,
        to: data.overall.end_value
      }, 1000, am4core.ease.cubicOut).start();
    }, 2000);

    return () => {
      chart.dispose();
    };
  }, [id, data, axisColor, axisLabelColor, initialHandValue, initialColor]);

  return (
    <div className="gauge-chart" id={id}></div>
  );
}

export default GaugeChart;