import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

import { convertToM } from '../../constants/formatter';

const StackedColumnChartTwo = (props) => {
  const {
          id,
          data,
          legend,
          largerWidth,
          isCardSmaller,
          isMediumCard,
          isOneHundredPercent 
        } = props;

  am4core.options.autoSetClassName = true;
  useLayoutEffect(() => {
    let categoryMinGridDistance = isCardSmaller ? 10 : 50;
    let labelFontSize = isCardSmaller && isMediumCard ? "0.65rem" : isCardSmaller ? "0.6rem" :"0.8rem";
    let chartPaddingTop = isCardSmaller && isMediumCard ? 50 : 30;
    let chart = am4core.create(id, am4charts.XYChart);

    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    chart.padding(chartPaddingTop, 0, 0, 0);
    chart.margin(0);
    chart.data = data

    // Create axes
    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.minGridDistance = categoryMinGridDistance;
    categoryAxis.renderer.grid.template.disabled = true;
    categoryAxis.renderer.labels.template.adapter.add("text", label => {
      return`[font-size: ${labelFontSize}]${label}[/]`;
    });
    categoryAxis.id = "myCategoryAxis";

    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.inside = true;
    valueAxis.renderer.grid.template.strokeWidth = 0;
    valueAxis.renderer.labels.template.disabled = true;
    valueAxis.renderer.grid.template.disabled = true;
    valueAxis.renderer.minGridDistance = 50;
    valueAxis.id = "myValueAxis";

    const createSeries = (field, name, color) => {
      let series = chart.series.push(new am4charts.ColumnSeries());
      let tooltipTextPercent = "[#fff]{name}: [#fff font-weight: bold]{valueY.formatNumber('#.0')}%[/][/]";

      if (largerWidth === true) {
        series.columns.template.width = am4core.percent(45);
      } else {
        series.columns.template.width = am4core.percent(34);
      }
      series.columns.template.fill = am4core.color(color);
      series.columns.template.stroke = am4core.color(color);
      series.columns.template.tooltipText = tooltipTextPercent;
      series.name = name;
      series.dataFields.categoryX = "category";
      series.dataFields.valueY = field;
      series.stacked = true;
      series.tooltip.pointerOrientation = "vertical";

      return series;
    };

    let seriesArr = [];
    for (let i = 0; i < legend.length; i++) {
      seriesArr.push(createSeries(legend[i].value_name, legend[i].name, legend[i].color));
    }

    if (isOneHundredPercent === true) {
      valueAxis.min = 0;
      valueAxis.max = 100;
      valueAxis.strictMinMax = true;
    } else {
      valueAxis.extraMax = 0.05;
      valueAxis.calculateTotals = true;

      let bullet = seriesArr[seriesArr.length - 1].bullets.push(new am4charts.LabelBullet());
      bullet.label.text = "{valueY.total}";
      bullet.label.fill = am4core.color("#748A9D");
      bullet.label.hideOversized = false;
      bullet.label.truncate = false;
      bullet.label.verticalCenter = "top";
      bullet.label.dy = -20;
      bullet.label.textAlign = "middle"
      bullet.label.adapter.add("text", (label, target, key) => {
        let total = target.dataItem.values.valueY.total;
        let defaultLabel = `[font-size: ${labelFontSize}]${convertToM(total)}[/]`;
        return defaultLabel;
      });
    }

    return () => {
      chart.dispose();
    };
  }, [
      id,
      data,
      legend,
      largerWidth,
      isCardSmaller,
      isMediumCard,
      isOneHundredPercent 
    ]
  );

  return (
    <div className="stacked-column-chart-two" id={id}></div>
  );
}

export default StackedColumnChartTwo;



