import React, { useState, useEffect } from 'react';
import './executiveCockpit.scss';
import axios from 'axios';
// filter components
import { optionMeasure, optionRange, optionGroup1, optionRegion, optionMetro } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD, SearchCheckboxDD, BasicDD } from '../../components/customDropdown';
import { FilterContainer } from '../../components/filter';
// comment components
import AddMetricsModalMetro from './AddMetricsModalMetro';
import Loader from '../../components/loader/Loader';
import DashboardLayout from '../../components/playground/dashboard-layout/dashboardLayout';
// constant
import { API_BASE_URL } from '../../constants/config';



const Metro = (props) => {
  // Axios call
  const { selectedItem, stepList, setStepList } = props;
  const [dashboardData, setDashboardData] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const [modalShow, setModalShow] = useState(false);
  const [metroOnApply, setMetroOnApply] = useState(false);
  const [filteredData, setFilteredData] = useState([]);
  const [selectedItemMetro, setSelectedItemMetro] = useState(selectedItem);

  const checker = (data, arr) => {
    if (arr) {
      let sampleData = data.filter(item => {
        let key = item.key;
        let result = arr.some(function (arrVal) {
          return arrVal.key === key;
        });
        return result;
      })
      setFilteredData(sampleData)
    }
  }

  const onCancelModal = () => {
    setModalShow(false);
  };

  const fetchPretty = () => {
    setIsLoading(true)
    axios({
      method: 'get',
      url: `${API_BASE_URL}/executive-cockpit-cards.json`,
    }).then(result => {
      setDashboardData(result.data);

      if (result.data) {
        checker(result.data, selectedItemMetro)
        // setUniqueData(getUnique(filteredData, 'key'))
      }
      setIsLoading(false)
      setMetroOnApply(false);
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchPretty();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [metroOnApply]);

  // checkbox for hq region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val)
  }
  const onCancelRegion = () => {
    set_region_val(region_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val)
  }
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply)
  }

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val)
  }
  const onCancelRange = () => {
    set_range_val(range_val_onApply)
  }

  // radio for group-by values
  const group_initial_value = optionGroup1 ? optionGroup1[0] : null;
  const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
  const [group_val, set_group_val] = useState(group_initial_value);
  const onApplyGroup = () => {
    set_group_val_onApply(group_val)
  }
  const onCancelGroup = () => {
    set_group_val(group_val_onApply)
  }

  // basic dd for product values
  const [product_option, set_product_option] = useState([
    {
      key: 'all_domains',
      text: 'All Domains'
    }
  ])
  const productOption = [
    {
      key: 'all_domains',
      text: 'All Domains'
    }, {
      key: 'rev',
      text: 'Revenue'
    }, {
      key: 'bkgs',
      text: 'Bookings'
    }, {
      key: 'churn',
      text: 'Churn'
    }, {
      key: 'pricing',
      text: 'Pricing'
    }, {
      key: 'cbt',
      text: 'Cross Border'
    }, {
      key: 'cp',
      text: 'Channel Performance'
    }, {
      key: 'nlae',
      text: 'New Logos Extension'
    }, {
      key: 'sp',
      text: 'Sales Performance (Team)'
    }, {
      key: 'rp',
      text: 'Rep Performance'
    }, {
      key: 'cc',
      text: 'Customer Count'
    }, {
      key: 'gp',
      text: 'Global Platform'
    }, {
      key: 'eco',
      text: 'Ecosystem'
    }, {
      key: 'cust',
      text: 'Customer NPS'
    }, {
      key: 'cm',
      text: 'Colo Market Growth'
    }, {
      key: 'ms',
      text: 'Market Share'
    }
  ]
  const product_initial_value = product_option ? product_option[0] : null;
  const [productSelection, setProductSelection] = useState(product_initial_value);

  const create_a_list_of_arr = () => {
    let check_what_are_selected = selectedItemMetro.map(item => {
      return item.key.toLowerCase().split('_')[0]
    })

    let update_product_option = check_what_are_selected.map(item => {
      let object_val = productOption.find(data => data.key === item)
      return object_val
    })

    set_product_option([
      {
        key: 'all_domains',
        text: 'All Domains'
      },
      ...update_product_option
    ])
  }

  useEffect(() => {
    if (selectedItemMetro.length > 0) {
      create_a_list_of_arr()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedItemMetro])

  if (isLoading) return <Loader withContainer />

  return (
    <div className={`page-content`}>
      <AddMetricsModalMetro
        onCancelModal={onCancelModal}
        showAddMetricsModal={modalShow}
        selectedItemMetro={selectedItemMetro}
        setSelectedItemMetro={setSelectedItemMetro}
        stepList={stepList}
        setStepList={setStepList}
        setMetroOnApply={setMetroOnApply}
      />
      <FilterContainer
        showAddMetric={true}
        setModalShow={setModalShow}
        onCancelModal={onCancelModal}
      >
        <div className="filters">
          <CheckboxDD
            title="Deploy Region"
            id="region"
            options={optionRegion}
            valueApplied={region_val_onApply}
            checkVal={region_val}
            setCheckVal={set_region_val}
            onApply={onApplyRegion}
            onCancel={onCancelRegion}
          />
          <SearchCheckboxDD
            title="Deploy Metro"
            id="metro"
            options={optionMetro}
            valueApplied={metro_val_onApply}
            checkVal={metro_val}
            setCheckVal={set_metro_val}
            onApply={onApplyMetro}
            onCancel={onCancelMetro}
          />
          <RadioDD
            title="Time Period"
            id="range"
            options={optionRange}
            radioVal={range_val}
            setRadioVal={set_range_val}
            onApply={onApplyRange}
            onCancel={onCancelRange}
          />
          <BasicDD
            title="All Categories"
            id="all_categories_cockpit"
            value={productSelection}
            setValue={setProductSelection}
            options={product_option}
          />
        </div>
      </FilterContainer>

      <div className="dashboard-container">
        <DashboardLayout
          data={filteredData}
          setData={setFilteredData}
          selectedItemMetro={selectedItemMetro}
          setSelectedItemMetro={setSelectedItemMetro}
          stepList={stepList}
          setStepList={setStepList}
          productSelection={productSelection}
        />
      </div>
    </div>
  )
};

export default Metro;

