import React, { useRef, useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { DashboardLayoutComponent, PanelsDirective, PanelDirective } from '@syncfusion/ej2-react-layouts';
import { Card, DropdownButton, Dropdown, Row, Col, } from 'react-bootstrap';
import KPI_NEW from '../../../components/kpi/KPI_NEW';
import Legend from '../../../components/legend/Legend';
import StackedColumnChart from '../../../components/chart/StackedColumnChart';
import { FiMoreVertical } from "react-icons/fi";
import { IoMdArrowDropright } from "react-icons/io";
import LineChart from '../../../components/chart/LineChart';
import GaugeChart from '../../../components/chart/GaugeChart';
import DonutChart from '../../../components/chart/DonutChart';
import StackedColumnChartTwo from '../../../components/chart/StackedColumnChartTwo';
import { convertToUSD, convertToCount, convertToWholePercent } from '../../../constants/formatter';
import { v4 as uuidv4 } from 'uuid';

import "./dashboardLayout.scss";

const DashboardLayout = (props) => {
  const { data, setData, selectedItemMetro, setSelectedItemMetro, stepList, setStepList, productSelection } = props;
  let history = useHistory();
  let dashboardObj = useRef(null);
  let showAsArrayList = ['total_revenue', 'customer_mrr'];
  let groupByArrayList = ['region', 'vertical', 'type'];

  const goTo = (url) => history.push('/analytics-dashboard' + url);
  const doubleClick = (event, url) => goTo(url);

  const onRemove = (panelId, key) => {
    dashboardObj.removePanel(panelId);
    const remove_selected_item = selectedItemMetro.filter(item => item.key !== key);
    setSelectedItemMetro(remove_selected_item)

    const new_steplist = stepList.map((val) => {
      if (val.key === key) {
        val.isChecked = false;
      }
      return val
    })

    setStepList(new_steplist)
  }

  const groupByFilter = (filterGroupName, id) => {
    let groupFilter = data.map(value => {
      if (value.key === id) {
        value.groupFilter = filterGroupName
        return value
      }

      return value
    })

    setData(groupFilter)
  }


  const showByFilter = (filterShowName, id) => {
    let showFilter = data.map(value => {
      if (value.key === id) {
        value.showFilter = filterShowName
        return value
      }

      return value
    })

    setData(showFilter)
  }

  const revertToDefault = (id) => {
    let revertItemToDefaultState = data.map(value => {
      if (value.key === id) {
        delete value.showFilter
        delete value.groupFilter
        return value
      }

      return value
    })

    setData(revertItemToDefaultState)
  }

  const smallCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    let groupByOptions = item.group_by_options ? item.group_by_options : null;
    let showAsOptions = item.show_as_options ? item.show_as_options : null;
    let totalUnit = item.overall.total.unit;
    let totalValue = item.overall.total.value;
    let details = item.overall.details;
    let total, quarterOverQuarter, yearOverYear;

    let bpsSpeedometerValueTextColor = item.chart_speedometer && item.chart_speedometer.details.value >= 5
      ? "overall-bps-positive"
      : item.chart_speedometer && item.chart_speedometer.details.value <= -5
        ? "overall-bps-negative"
        : data.value >= 5 && data.value <= -5
          ? "overall-bps-middle" : '';
    if (details) {
      quarterOverQuarter = details.filter(data => {
        let checkerData;
        let includeQuarter = data.label.includes("Q/Q");
        if (includeQuarter) {
          checkerData = data
        }
        return checkerData;
      });
      yearOverYear = details.filter(data => {
        let checkerData;
        let includeYearly = data.label.includes("Y/Y");
        if (includeYearly) {
          checkerData = data
        }
        return checkerData;
      });
    }

    if (totalUnit && totalUnit === "$") {
      let convertedTotal = convertToUSD(totalValue);

      if (/M/g.test(convertedTotal) === false) {
        total = <div className={`total-revenue-sm no-hover`}>
          <div className="with-ellipsis">
            <span style={{ fontSize: '0.6rem' }}>{convertedTotal && convertedTotal.slice(0, 1)}</span>
            <b style={{ fontSize: '1rem', lineHeight: '1.5' }}>{convertedTotal && convertedTotal.slice(1)}</b>
          </div>
        </div>;
      } else {
        total = <div className={`total-revenue-sm no-hover`}>
          <div className="with-ellipsis">
            <span style={{ fontSize: '0.6rem' }}>{convertedTotal.slice(0, 1)}</span>
            <span style={{ fontSize: '1rem', lineHeight: '1.5' }}>{convertedTotal.slice(1, -1)}</span>
            <span style={{ fontSize: '0.6rem' }}>{convertedTotal.slice(-1)}</span>
          </div>
        </div>;
      }
    } else if (totalUnit && totalUnit === "%") {
      let convertedTotal = convertToWholePercent(totalValue);

      total = <div className={`total-revenue-sm no-hover`}>
        <div className="with-ellipsis">
          <span style={{ fontSize: '1rem', lineHeight: '1.5' }}>{convertedTotal}</span>%
          </div>
      </div>;
    }

    if (details) {
      quarterOverQuarter = details.filter(data => {
        let checkerData;
        let includeQuarter = data.label.includes("Q/Q");
        if (includeQuarter) {
          checkerData = data
        }
        return checkerData;
      });

      yearOverYear = details.filter(data => {
        let checkerData;
        let includeYearly = data.label.includes("Y/Y");
        if (includeYearly) {
          checkerData = data
        }
        return checkerData;
      });
    }




    return (
      <Card.Body
        id={`card-${panelId}`}
        onDoubleClick={(e) => { doubleClick(e, item.url) }}
        className={`small-card custom-scroll overflow-auto ${item.chart.isLine ? 'line-height' : ''} ${size === 'S' ? 'd-block' : 'd-none'}`}>
        <Card.Title className="with-ellipsis" style={{ maxWidth: '145px' }}>
          {item.key === 'rev_usd_m' && item.showFilter
            ? ` ${item.showFilter.replace(/_/g, ' ')} $M`
            : item.key === 'rev_usd_m' ? 'total ' + item.title
              : item.key === 'rev_top_10_metro_rev_mix_%' && item.showFilter
                ? ` ${item.showFilter.replace(/_/g, ' ')} metro rev mix %`
                : item.title
          }
        </Card.Title>
        <div className="button-container">
          <DropdownButton id={`dropdown-basic-button`} drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
            <Dropdown.Item onClick={() => { onRemove(panelId, item.key) }}>Delete Card</Dropdown.Item>
            {item.key === 'rev_growth_%' && <Dropdown.Item onClick={() => { revertToDefault(item.key) }}>Revert to Default</Dropdown.Item>}
            {availableSizes && availableSizes.map((element, i) => {
              let unique = uuidv4()
              return (
                <Dropdown.Item
                  key={i + unique}
                  className={size
                    ? size !== element.size
                      ? 'd-block'
                      : 'd-none'
                    : item.layout.defaultSize !== element.size
                      ? 'd-block'
                      : 'd-none'
                  }
                  onClick={() => {
                    onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                  }}>Resize to {element.size}</Dropdown.Item>
              )
            })}
            {(groupByOptions && groupByOptions.length > 0) &&
              <>
                <DropdownButton className="dropdown-submenu" id={`dropdown-basic-button-2`} variant="link" drop={'right'} title={
                  <div className="filter-title">Group by<IoMdArrowDropright /></div>
                }>
                  {groupByOptions.length > 0 && groupByOptions.map((element, i) => (
                    <Dropdown.Item id={'dropdown-group-' + element.key} key={'dropdown-group-' + element.key} className="group-by-filter" onClick={() => { groupByFilter(element.key, item.key) }}>
                      {element.title}
                    </Dropdown.Item>
                  ))}
                </DropdownButton>
              </>
            }
            {(showAsOptions && showAsOptions.length > 0) &&
              <>
                <DropdownButton className="dropdown-submenu" id={`dropdown-basic-button-3`} variant="link" drop={'right'} title={
                  <div className="filter-title">Show as<IoMdArrowDropright /></div>
                }>
                  {showAsOptions.length > 0 && showAsOptions.map((element) => (
                    <Dropdown.Item id={'dropdown-show-' + element.key} key={'dropdown-show-' + element.key} className="group-by-filter" onClick={() => { showByFilter(element.key, item.key) }}>
                      {element.title}
                    </Dropdown.Item>
                  ))}
                </DropdownButton>
              </>
            }
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
        </div >
        <div className={`${!item.chart_speedometer && !item.chart.isDonut && !item.donut_chart ? 'kpi-container-sm ' : 'd-none'} `}>
          {!item.chart_speedometer && !item.chart.isDonut && !item.donut_chart ?
            <Row>
              <Col md={6} className="pr-0">
                {!item.chart_speedometer ? total : null}
              </Col>

              <Col md={6} className="pr-0">
                {details && details.map((data, key) => {
                  let unique_key = uuidv4();
                  let quarterOverQuarter = null;
                  let includeQuarter = data.label.includes("Q/Q");
                  if (includeQuarter) {
                    quarterOverQuarter = data
                  }
                  let yearOverYear = null;
                  let includeYearly = data.label.includes("Y/Y");
                  if (includeYearly) {
                    yearOverYear = data
                  }
                  let yearOverYearTextColor = yearOverYear >= 0 ? "overall-bps-positive" : yearOverYear <= 0 ? "overall-bps-negative" : yearOverYear === 0 ? "overall-bps-middle" : '';
                  let bpsValueTextColor = data.value >= 5 ? "overall-bps-positive" : data.value <= -5 ? "overall-bps-negative" : data.value >= 5 && data.value <= -5 ? "overall-bps-middle" : '';
                  return (
                    <div className="with-ellipsis" key={unique_key} style={{ paddingTop: key === 0 ? '0.3rem' : 'none' }}>
                      {item.key.includes("%") && yearOverYear ?
                        <div className={yearOverYearTextColor}>
                          <span style={{ fontSize: '0.6rem' }}>{yearOverYear ? 'Y/Y  ' : null}</span>
                          <span style={{ fontSize: '0.95rem', lineHeight: '1' }}>{yearOverYear.value > 5 ? '+' : '-'}{yearOverYear ? yearOverYear.value : null}</span>
                          <span style={{ fontSize: '0.6rem' }}>{yearOverYear ? '%' : null}</span>
                        </div>
                        : quarterOverQuarter ?
                          <>
                            <span style={{ fontSize: '0.6rem' }}>{quarterOverQuarter ? 'Q/Q  ' : null}</span>
                            {/* <span className={bpsValueTextColor}>{item.unit === "bps" && item.value > 0 ? "+" : ""}{convertToCount(item.value)}</span><span className={bpsValueTextColor}>{item.unit ? item.unit : null}</span> */}
                            <span style={{ fontSize: '0.95rem', lineHeight: '1' }}>{quarterOverQuarter.value > 5 ? '+' : '-'}{quarterOverQuarter ? quarterOverQuarter.value : null}</span>
                            <span style={{ fontSize: '0.6rem' }}>%</span>
                          </>
                          : data.unit === 'bps' && item.key === 'rev_growth_%' && data.label.includes('Y-Y')
                          &&
                          <>
                            <span style={{ fontSize: '0.6rem' }} style={{ fontSize: '0.6rem' }}>Y-Y </span>
                            <span style={{ fontSize: '0.95rem', lineHeight: '1' }} className={`overall-bps ${bpsValueTextColor}`}>{data.value && data.value > 5 ? '+' : '-'}{convertToCount(data.value)}</span>
                            <span style={{ fontSize: '0.6rem' }} className={`overall-bps ${bpsValueTextColor}`}>{data.unit && data.unit}</span>
                          </>
                      }
                    </div>
                  )
                })
                }
              </Col>
            </Row>
            : null}
        </div>


        <div className={item.chart.isDonut || item.donut_chart ? 'chart-container-donut-sm' : !item.chart_speedometer ? 'chart-container-sm' : ''} >
          {item.chart_speedometer &&
            <div style={{ height: '100%', margin: '-0.5rem -1.5rem -1.3rem' }}>
              <GaugeChart
                id={'executive-cockpit-small' + item._id}
                data={item.chart_speedometer}
              />
              <div style={{ position: 'absolute', top: '77%', left: '22%', fontSize: '0.8rem' }}>
                <span className={bpsSpeedometerValueTextColor}>
                  {item.chart_speedometer.details.value > 5 ? '+' : '-'} {item.chart_speedometer.details.value}{item.chart_speedometer.details.unit}
                </span>
                <span style={{ fontSize: '0.7rem' }}> vs. per Plan</span>
              </div>
            </div>
          }

          {item.chart.isLine &&
            <div style={{ height: '100%', marginTop: '-0.5rem' }}>
              {item.groupFilter === 'vertical' ?
                <LineChart
                  id={'vertical-executive-stacked-small' + item._id}
                  data={item.chart_by_vertical.data}
                  legend={item.chart_by_vertical.legend}
                  hideLabel={false}
                  hideValueLabel={true}
                  isCardSmaller={true}
                  isExecCardSmaller={true}
                  isPercentage={true}
                  isOneHundredPercent={true}
                  withLabel={true}
                  withDottedLine={false}
                  withGridLines={false}
                  dasharrayValues={"8,4"}
                  isAnimationEnabled={false}
                />
                : item.groupFilter === 'region' ?
                  <LineChart
                    id={'region-executive-stacked-small' + item._id}
                    data={item.chart_by_region.data}
                    legend={item.chart_by_region.legend}
                    hideLabel={false}
                    hideValueLabel={true}
                    isCardSmaller={true}
                    isExecCardSmaller={true}
                    isPercentage={true}
                    isOneHundredPercent={true}
                    withLabel={true}
                    withDottedLine={false}
                    withGridLines={false}
                    dasharrayValues={"8,4"}
                    isAnimationEnabled={false}
                  />
                  : item.showFilter === 'normalized' && item.groupFilter === 'vertical' ?
                    <LineChart
                      id={'region-executive-stacked-small' + item._id}
                      data={item.chart_normalized_by_vertical.data}
                      legend={item.chart_normalized_by_vertical.legend}
                      hideLabel={false}
                      hideValueLabel={true}
                      isCardSmaller={true}
                      isExecCardSmaller={true}
                      isPercentage={true}
                      isOneHundredPercent={true}
                      withLabel={true}
                      withDottedLine={false}
                      withGridLines={false}
                      dasharrayValues={"8,4"}
                      isAnimationEnabled={false}
                    />
                    : item.showFilter === 'normalized' && !item.groupFilter ?
                      <LineChart
                        id={'region-executive-stacked-small' + item._id}
                        data={item.chart_normalized.data}
                        legend={item.chart_normalized.legend}
                        hideLabel={false}
                        hideValueLabel={true}
                        isCardSmaller={true}
                        isExecCardSmaller={true}
                        isPercentage={true}
                        isOneHundredPercent={true}
                        withLabel={true}
                        withDottedLine={false}
                        withGridLines={false}
                        dasharrayValues={"8,4"}
                        isAnimationEnabled={false}
                      />
                      :
                      item.chart.isLine &&
                      <LineChart
                        id={'executive-stacked-small' + item._id}
                        data={item.chart.data}
                        legend={item.chart.legend}
                        hideLabel={false}
                        hideValueLabel={true}
                        isCardSmaller={true}
                        isExecCardSmaller={true}
                        isPercentage={true}
                        isOneHundredPercent={true}
                        withLabel={true}
                        withDottedLine={false}
                        withGridLines={false}
                        dasharrayValues={"8,4"}
                        isAnimationEnabled={false}
                      />
              }
            </div>

          }

          {
            item.chart.isStackedColumn &&
            <div style={{ height: '100%', marginTop: '-0.2rem' }}>
              <StackedColumnChartTwo
                id={'executive-cockpit-small' + item._id}
                data={item.chart.data}
                legend={item.chart.legend}
                isCardSmaller={true}
                largerWidth={true}
                startValue={null}
                endValue={null}
                stackedColumnSelection={null}
                selectedRangeValue={null}
                percentLabel={false}
                insideLabel={false}
                hiddenValueAxis={false}
                hideLabelBullet={false}
                isPercentage={false}
                isOneHundredPercent={false}
                isOverviewChart={false}
                isGrowthPercentToggable={false}
                isAnimationEnabled={true}
                withNegativeValues={false}
                withMedianValue={false}
                medianValueName={null}
                disablePaddingTop={false}
                categoryAxisLabelDisabled={false}
                style={{ marginTop: '100px' }}

              />
            </div>
          }
          {item.chart.isDonut
            ?
            <div style={{ height: '100%', margin: '-1rem ​-0.5rem -1.3rem' }}>
              <DonutChart
                id={"executive-cockpit-small" + item._id}
                // totalTitle={"rev %"}
                total={item.key === 'rev_ent_rev_%' ? item.chart.rev_ent_rev_percent : null}
                data={item.chart.data}
                isTotalPercent={true}
                isMiniChart={true}
                legend={item.chart.legend}
                isAnimationEnabled={true}
                yearToYearValue={item.chart.sub_details && item.chart.sub_details[0].value}
                yearToYearUnit={item.chart.sub_details && item.chart.sub_details[0].unit}
                yearToYearLabel={item.chart.sub_details && item.chart.sub_details[0].label}
              />
            </div>
            : item.donut_chart_super_9 &&
            <div style={{ height: '100%', margin: '-1rem ​-0.5rem -1.3rem' }}>
              <DonutChart
                id={"executive-cockpit-small" + item._id}
                // totalTitle={"mix %"}
                total={item.key === 'rev_ent_rev_%' ? item.donut_chart.rev_ent_rev_percent : item.key === 'rev_top_10_metro_rev_mix_%' && item.donut_chart_super_9.rev_top_10_metro_rev_mix_}
                data={item.donut_chart.data}
                isTotalPercent={true}
                isMiniChart={true}
                legend={item.chart.legend}
                isAnimationEnabled={true}
                yearToYearValue={item.donut_chart_super_9.sub_details && item.donut_chart_super_9.sub_details[0].value}
                yearToYearUnit={item.donut_chart_super_9.sub_details && item.donut_chart_super_9.sub_details[0].unit}
                yearToYearLabel={item.donut_chart_super_9.sub_details && item.donut_chart_super_9.sub_details[0].label}
              />
            </div>}
        </div>
      </Card.Body >
    )
  }
  const mediumCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    let details = item.overall.details;
    let totalUnit = item.overall.total.unit;
    let totalValue = item.overall.total.value;
    let groupByOptions = item.group_by_options ? item.group_by_options : [];
    let showAsOptions = item.show_as_options ? item.show_as_options : [];
    let total;

    if (totalUnit && totalUnit === "$") {
      let convertedTotal = convertToUSD(totalValue);
      if (/M/g.test(convertedTotal) === false) {
        total = <>
          <div className={`total-revenue-md no-hover with-ellipsis`}>
            {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1)}</span>
          </div>
        </>;
      } else {
        total = <>
          <div className={`total-revenue-md no-hover with-ellipsis`}>
            {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1, -1)}</span> {convertedTotal.slice(-1)}
          </div>
        </>;
      }
    } else if (totalUnit && totalUnit === "%") {
      let convertedTotal = convertToWholePercent(totalValue);
      total = <>
        <div className={`total-revenue-md no-hover with-ellipsis`}>
          <span>{convertedTotal}</span>%
          </div>
      </>;
    }

    return (
      <Card.Body
        id={`card-${panelId}`}
        onDoubleClick={(e) => { doubleClick(e, item.url) }}
        className={`overflow-auto custom-scroll medium-card ${size === 'M' ? 'd-block' : 'd-none'}`} >
        <Card.Title className="with-ellipsis" style={{ maxWidth: '150px' }}>
          {item.key === 'rev_usd_m' && item.showFilter
            ? ` ${item.showFilter.replace(/_/g, ' ')} $M`
            : item.key === 'rev_usd_m' ? 'total ' + item.title
              : item.key === 'rev_top_10_metro_rev_mix_%' && item.showFilter
                ? ` ${item.showFilter.replace(/_/g, ' ')} metro rev mix %`
                : item.title
          }
        </Card.Title>
        <div className={`${details.length > 2 ? 'four-kpi-container-md' : 'two-kpi-container-md'} `}>
          <div className="button-container">
            <DropdownButton id="dropdown-basic-button" drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
              <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
              <Dropdown.Item onClick={() => { onRemove(panelId, item.key) }}>Delete Card</Dropdown.Item>
              {item.key === 'rev_growth_%' && <Dropdown.Item onClick={() => { revertToDefault(item.key) }}>Revert to Default</Dropdown.Item>}
              {availableSizes.map((element, i) => {
                let unique = uuidv4()
                return (
                  <Dropdown.Item
                    key={i + unique}
                    className={size
                      ? size !== element.size
                        ? 'd-block'
                        : 'd-none'
                      : item.layout.defaultSize !== element.size
                        ? 'd-block'
                        : 'd-none'
                    }
                    onClick={() => {
                      onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                    }}>Resize to {element.size}</Dropdown.Item>
                )
              })}
              {groupByOptions.length > 0 &&
                <>
                  <DropdownButton
                    className="dropdown-submenu"
                    id={`dropdown-basic-button-2`}
                    variant="link"
                    drop={'right'}
                    title={<div className="filter-title">Group by<IoMdArrowDropright /></div>}>
                    {groupByOptions.length > 0 && groupByOptions.map((element, i) => (
                      <Dropdown.Item key={`group-by-${panelId}`} id={'dropdown-' + element.key} className="group-by-filter" onClick={() => { groupByFilter(element.key, item.key) }}>
                        {element.title}
                      </Dropdown.Item>
                    ))}
                  </DropdownButton>
                </>
              }
              {showAsOptions.length > 0 &&
                <>
                  <DropdownButton
                    className="dropdown-submenu"
                    id={`dropdown-basic-button-3`}
                    variant="link"
                    drop={'right'}
                    title={<div className="filter-title">Show as<IoMdArrowDropright /></div>}>
                    {showAsOptions.length > 0 && showAsOptions.map((element) => (
                      <Dropdown.Item id={'dropdown-' + element.key} className="group-by-filter" onClick={() => { showByFilter(element.key, item.key) }}>
                        {element.title}
                      </Dropdown.Item>
                    ))}
                  </DropdownButton>
                  <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
                </>
              }
            </DropdownButton>
          </div>
          {total}
          <Row>
            {details.map((item, index) => {
              let unique = uuidv4();
              let bpsValueTextColor = item.value >= 5 ? "overall-bps-positive" : item.value <= -5 ? "overall-bps-negative" : item.value >= 5 && item.value <= -5 ? "overall-bps-middle" : '';
              return (
                <Col md={6} xs={6} key={index + unique} className="pr-0">
                  <div className={` kpi-item no-hover`}>
                    <div className="title">{item.label}</div>
                    <div className="with-ellipsis">
                      {/%/g.test(item.label) === true
                        ? item.value
                          ? <><span className="value" style={{ paddingTop: '0.5rem' }}> {item.value && item.value > 5 ? '+' : '-'}{convertToWholePercent(item.value)}</span>%</>
                          : <span>-</span>
                        : /\$/g.test(item.label) === true
                          ? item.value
                            ? <span>{item.value && item.value > 5 ? '+' : '-'}{convertToUSD(item.value)}</span>
                            : <span>-</span>
                          : item.value
                            ? <span className={`overall-bps ${bpsValueTextColor}`}>{item.value && item.value > 5 ? '+' : '-'}{convertToCount(item.value)} {item.unit && item.unit}</span>
                            : <span>-</span>
                      }
                    </div>
                  </div>
                </Col>
              )
            })}
          </Row>
        </div>
        {item.groupFilter === 'vertical' ?
          <Legend legend={item.chart_by_vertical.legend} isSmallerFont={true} />
          : item.groupFilter === 'region'
            ? <Legend legend={item.chart_by_region.legend} isSmallerFont={true} />
            : item.showFilter === 'normalized' && item.groupFilter === 'vertical'
              ? <Legend legend={item.chart_normalized_by_vertical.legend} isSmallerFont={true} />
              : item.showFilter === 'normalized' && !item.groupFilter
                ? <Legend legend={item.chart_normalized.legend} isSmallerFont={true} />
                : item.key != 'rev_ent_rev_%' &&
                <Legend legend={item.chart.legend} isSmallerFont={true} />

        }
        <div className={`${details.length > 2 ? 'four-kpi-chart-container-md' : 'two-kpi-chart-container-md'} `}
          style={{ marginTop: item.chart.isStackedColumn100 && '0.6rem' }}>
          {
            item.groupFilter === 'vertical' ?
              <LineChart
                id={'vertical-executive-stacked-medium' + item._id}
                data={item.chart_by_vertical.data}
                legend={item.chart_by_vertical.legend}
                hideLabel={false}
                hideValueLabel={true}
                isCardSmaller={true}
                isExecCardSmaller={true}
                isPercentage={true}
                isOneHundredPercent={true}
                withLabel={true}
                withDottedLine={false}
                withGridLines={false}
                dasharrayValues={"8,4"}
                isAnimationEnabled={false}
              />
              : item.groupFilter === 'region' ?
                <LineChart
                  id={'region-executive-stacked-medium' + item._id}
                  data={item.chart_by_region.data}
                  legend={item.chart_by_region.legend}
                  hideLabel={false}
                  hideValueLabel={true}
                  isCardSmaller={true}
                  isExecCardSmaller={true}
                  isPercentage={true}
                  isOneHundredPercent={true}
                  withLabel={true}
                  withDottedLine={false}
                  withGridLines={false}
                  dasharrayValues={"8,4"}
                  isAnimationEnabled={false}
                />
                : item.showFilter === 'normalized' && item.groupFilter === 'vertical' ?
                  <LineChart
                    id={'region-executive-stacked-medium' + item._id}
                    data={item.chart_normalized_by_vertical.data}
                    legend={item.chart_normalized_by_vertical.legend}
                    hideLabel={false}
                    hideValueLabel={true}
                    isCardSmaller={true}
                    isExecCardSmaller={true}
                    isPercentage={true}
                    isOneHundredPercent={true}
                    withLabel={true}
                    withDottedLine={false}
                    withGridLines={false}
                    dasharrayValues={"8,4"}
                    isAnimationEnabled={false}
                  />
                  : item.showFilter === 'normalized' && !item.groupFilter ?
                    <LineChart
                      id={'region-executive-stacked-medium' + item._id}
                      data={item.chart_normalized.data}
                      legend={item.chart_normalized.legend}
                      hideLabel={false}
                      hideValueLabel={true}
                      isCardSmaller={true}
                      isExecCardSmaller={true}
                      isPercentage={true}
                      isOneHundredPercent={true}
                      withLabel={true}
                      withDottedLine={false}
                      withGridLines={false}
                      dasharrayValues={"8,4"}
                      isAnimationEnabled={false}
                    />
                    : item.key === 'rev_ent_rev_%' ?
                      <LineChart
                        id={'executive-stacked-medium' + item._id}
                        data={item.line_chart_ww.data}
                        legend={item.line_chart_ww.legend}
                        hideLabel={false}
                        hideValueLabel={true}
                        isCardSmaller={true}
                        isExecCardSmaller={true}
                        isPercentage={true}
                        isOneHundredPercent={true}
                        withLabel={true}
                        withDottedLine={false}
                        withGridLines={false}
                        dasharrayValues={"8,4"}
                        isAnimationEnabled={false}
                      />
                      : item.chart.isLine &&
                      <LineChart
                        id={'executive-stacked-medium' + item._id}
                        data={item.chart.data}
                        legend={item.chart.legend}
                        hideLabel={false}
                        hideValueLabel={true}
                        isCardSmaller={true}
                        isExecCardSmaller={true}
                        isPercentage={true}
                        isOneHundredPercent={true}
                        withLabel={true}
                        withDottedLine={false}
                        withGridLines={false}
                        dasharrayValues={"8,4"}
                        isAnimationEnabled={false}
                      />
          }
          {
            item.chart.isStackedColumn &&
            <>

              {/* <div style={{ height: details.length > 2 ? '60%' : '45%', paddingTop: '0.5rem' }}> */}
              <StackedColumnChartTwo
                className="medium-stacked-column"
                id={'executive-stacked-medium' + item._id}
                data={item.chart.data}
                legend={item.chart.legend}
                largerWidth={true}
                isAnimationEnabled={true}
                isCardSmaller={true}
                isMediumCard={details.length > 2 ? true : false}
              />
              {/* </div> */}
            </>
          }
          {
            item.chart.isStackedColumn100 &&
            <>
              {/* <div style={{ height: details.length > 2 ? '60%' : '45%', paddingTop: '0.5rem' }}> */}
              <StackedColumnChartTwo
                className="medium-stacked-column"
                id={'executive-stacked-medium' + item._id}
                data={item.chart.data}
                legend={item.chart.legend}
                largerWidth={true}
                isAnimationEnabled={true}
                isCardSmaller={true}
                isMediumCard={details.length > 2 ? true : false}
                isOneHundredPercent={true}
              />
              {/* </div> */}
            </>
          }
        </div>

      </Card.Body >
    )
  }
  const largeCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    let groupByOptions = item.group_by_options ? item.group_by_options : [];
    let showAsOptions = item.show_as_options ? item.show_as_options : [];

    return (
      <Card.Body
        id={`card-${panelId}`}
        onDoubleClick={(e) => { doubleClick(e, item.url) }}
        className={`${size === 'L' ? 'd-block' : 'd-none'}`}>
        <Card.Title className="with-ellipsis">
          {item.key === 'rev_usd_m' && item.showFilter
            ? ` ${item.showFilter.replace(/_/g, ' ')} $M`
            : item.key === 'rev_usd_m' ? 'total ' + item.title
              : item.title}
        </Card.Title>
        <div className="button-container">
          <DropdownButton id="dropdown-basic-button" drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
            <Dropdown.Item onClick={() => { onRemove(panelId, item.key) }}>Delete Card</Dropdown.Item>
            {item.key === 'rev_growth_%' && <Dropdown.Item onClick={() => { revertToDefault(item.key) }}>Revert to Default</Dropdown.Item>}
            {availableSizes.map((element, i) => {
              let unique = uuidv4()
              return (
                <Dropdown.Item
                  key={i + unique}
                  className={size
                    ? size !== element.size
                      ? 'd-block'
                      : 'd-none'
                    : item.layout.defaultSize !== element.size
                      ? 'd-block'
                      : 'd-none'
                  }
                  onClick={() => {
                    onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                  }}>Resize to {element.size}</Dropdown.Item>
              )
            })}

            {groupByOptions.length > 0 &&
              <>
                <DropdownButton className="dropdown-submenu" id={`dropdown-basic-button-2`} variant="link" drop={'right'} title={
                  <div className="filter-title">Group by<IoMdArrowDropright /></div>
                }>
                  {groupByOptions.length > 0 && groupByOptions.map((element, i) => (
                    <Dropdown.Item key={"group-" + i} id={'dropdown-' + element.key} className="group-by-filter" onClick={() => {
                      groupByFilter(element.key, item.key)
                    }}>
                      {element.title}
                    </Dropdown.Item>
                  ))}
                </DropdownButton>
              </>
            }

            {showAsOptions.length > 0 &&
              <>
                <DropdownButton className="dropdown-submenu" id={`dropdown-basic-button-3`} variant="link" drop={'right'} title={
                  <div className="filter-title">Show as<IoMdArrowDropright /></div>
                }>
                  {showAsOptions.length > 0 && showAsOptions.map((element) => (
                    <Dropdown.Item id={'dropdown-' + element.key} className="group-by-filter"
                      onClick={() => {
                        showByFilter(element.key, item.key)
                      }}>
                      {element.title}
                    </Dropdown.Item>
                  ))}
                </DropdownButton>
                <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
              </>
            }
          </DropdownButton>
        </div>

        <div className="d-flex flex-row large-card">
          <div className="d-flex flex-column">
            {item && item.showFilter === 'customer_mrr'
              ? <KPI_NEW
                totalValue={item.overall_customer_mrr.total.value}
                totalUnit={item.overall_customer_mrr.total.unit}
                details={item.overall_customer_mrr.details}
                noHover={true}
              />
              :
              <KPI_NEW
                totalValue={item.overall.total.value}
                totalUnit={item.overall.total.unit}
                details={item.overall.details}
                noHover={true}
              />
            }


          </div>
          <div className="d-flex flex-column">
            <Legend legend={item.chart.legend} />
            {item && item.showFilter === 'customer_mrr'
              ? <>
                {item.chart_customer_mrr.isLine &&
                  <div style={{ height: '100%', paddingTop: '1.5rem' }}>
                    <LineChart
                      id={'executive-stacked-large' + item._id}
                      data={item.chart_customer_mrr.data}
                      legend={item.chart_customer_mrr.legend}
                      hideLabel={false}
                      hideValueLabel={true}
                      isCardSmaller={true}
                      isPercentage={true}
                      isOneHundredPercent={true}
                      withLabel={true}
                      withDottedLine={false}
                      withGridLines={false}
                      dasharrayValues={"8,4"}
                      isAnimationEnabled={false}
                    />
                  </div>
                }
                {item.chart_customer_mrr.isStackedColumn &&
                  <StackedColumnChart
                    className="large-stacked-column"
                    id={'executive-stacked-large' + item._id}
                    data={item.chart_customer_mrr.data}
                    legend={item.chart_customer_mrr.legend}
                    largerWidth={true}
                    startValue={null}
                    endValue={null}
                    stackedColumnSelection={null}
                    selectedRangeValue={null}
                    percentLabel={false}
                    insideLabel={false}
                    hiddenValueAxis={true}
                    hideLabelBullet={false}
                    isPercentage={false}
                    isOneHundredPercent={false}
                    isOverviewChart={false}
                    isGrowthPercentToggable={false}
                    isAnimationEnabled={false}
                    isCardSmaller={true}
                    withNegativeValues={false}
                    withMedianValue={false}
                    medianValueName={null}
                    disablePaddingTop={false}
                    categoryAxisLabelDisabled={false}
                  />
                }
              </>
              : <>
                {item.chart.isLine &&
                  <div style={{ height: '100%', paddingTop: '1.5rem' }}>
                    <LineChart
                      id={'executive-stacked-large' + item._id}
                      data={item.chart.data}
                      legend={item.chart.legend}
                      hideLabel={false}
                      hideValueLabel={true}
                      isCardSmaller={true}
                      isPercentage={true}
                      isOneHundredPercent={true}
                      withLabel={true}
                      withDottedLine={false}
                      withGridLines={false}
                      dasharrayValues={"8,4"}
                      isAnimationEnabled={false}
                    />
                  </div>
                }




                {item.chart.isStackedColumn &&
                  <StackedColumnChart
                    className="large-stacked-column"
                    id={'executive-stacked-large' + item._id}
                    data={item.chart.data}
                    legend={item.chart.legend}
                    largerWidth={true}
                    startValue={null}
                    endValue={null}
                    stackedColumnSelection={null}
                    selectedRangeValue={null}
                    percentLabel={false}
                    insideLabel={false}
                    hiddenValueAxis={true}
                    hideLabelBullet={false}
                    isPercentage={false}
                    isOneHundredPercent={false}
                    isOverviewChart={false}
                    isGrowthPercentToggable={false}
                    isAnimationEnabled={false}
                    isCardSmaller={true}
                    withNegativeValues={false}
                    withMedianValue={false}
                    medianValueName={null}
                    disablePaddingTop={false}
                    categoryAxisLabelDisabled={false}
                  />
                }
              </>
            }
          </div>
        </div>
      </Card.Body >
    )
  }

  const onResize = async (id, sizeX, sizeY, size, item) => {
    dashboardObj.resizePanel(id, sizeX, sizeY)

    let sizeUpdated = data.map(value => {
      if (value.key === id) {
        value.layout.defaultSize = size;
        value.layout.defaultSizeX = sizeX;
        value.layout.defaultSizeY = sizeY;

        return value
      }

      return value
    })

    setData(sizeUpdated)
  }

  const PanelItems = data.map((item, i) => {
    let size = item.layout.defaultSize;

    let col_number;
    if (i % 3 === 0) {
      col_number = 0
    } else if (i % 3 === 2) {
      col_number = 4
    } else {
      col_number = 2
    }

    console.log(i)
    console.log(item)

    return (
      <PanelDirective
        className="panel-class"
        key={item.key}
        id={item.key}
        sizeX={size === 'S' ? 1 : size === 'M' ? 1 : size === 'L' ? 2 : 1}
        sizeY={size === 'S' ? 1 : size === 'M' ? 2 : size === 'L' ? 2 : 1}
        minSizeX={1}
        minSizeY={1}
        maxSizeX={2}
        maxSizeY={2}
        row={i}
        col={col_number}
        content={() =>
          <>
            {smallCard(item, item.key, size)}
            {mediumCard(item, item.key, size)}
            {largeCard(item, item.key, size)}
          </>
        }
      />
    )
  })
  return (
    <DashboardLayoutComponent
      id="executive-cockpit"
      key={'executive-cockpit'}
      columns={6}
      allowResizing={false}
      allowDragging={true}
      cellAspectRatio={80 / 80}
      cellSpacing={[30, 30]}
      mediaQuery={"max-width: 700px"}
      ref={(scope) => {
        dashboardObj = scope;
      }}
    >
      <PanelsDirective
        id="executive-cockpit-card"
        key={'executive-cockpit-card'}
      >
        {productSelection.key === 'all_domains'
          ? PanelItems
          : PanelItems.filter((child) => !productSelection.key || child.props.id.toLowerCase().split('_')[0] === productSelection.key)
        }
      </PanelsDirective>
    </DashboardLayoutComponent>
  )
};

export default DashboardLayout;