import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import { convertToChartTotalUSD, convertToWholeValueUSD, convertToChartValue, convertToWholePercent } from '../../constants/formatter';

am4core.useTheme(am4themes_animated);


const DonutChart = (props) => {
  const { data, id, isTotalPercent, isCount, legend, total, totalTitle, isDollar, isDollarButWhole, isMiniChart, yearToYearValue, yearToYearUnit, yearToYearLabel, isAnimationEnabled } = props

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.PieChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    if (isMiniChart) {
      chart.padding(-10, -12, -10, -12);
    } else {
      chart.padding(50, 0, 0, 0);
    }
    chart.margin(0);
    chart.data = data;
    chart.innerRadius = isMiniChart ? am4core.percent(60) : am4core.percent(50);

    let pieSeries = chart.series.push(new am4charts.PieSeries());
    pieSeries.dataFields.value = "value";
    pieSeries.dataFields.category = "segment";
    pieSeries.hiddenState.properties.opacity = 1;
    pieSeries.hiddenState.properties.endAngle = -90;
    pieSeries.hiddenState.properties.startAngle = -90;
    pieSeries.ticks.template.disabled = true;
    pieSeries.alignLabels = false;
    pieSeries.labels.template.text = "[font-size: 12px]{value.percent.formatNumber('#.0')}%[/]";
    pieSeries.labels.template.radius = am4core.percent(-20);
    pieSeries.labels.template.fill = am4core.color("white");
    pieSeries.slices.template.tooltipText = "[#fff]{category}: [#fff font-weight: bold]{value.formatNumber('#.0')}%[/][/]";
    pieSeries.colors.list = legend.sort((first, second) => (first._id - second._id)).map(legend => (am4core.color(legend.color)));
    pieSeries.hiddenState.transitionDuration = 0;
    pieSeries.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;

    let label = pieSeries.createChild(am4core.Label);
    label.text = isTotalPercent === true
      ? `${convertToWholePercent(total)}%`
      : isDollar === true
        ? isDollarButWhole === true ? `${convertToWholeValueUSD(total)}` : `${convertToChartTotalUSD(total)}`
        : isCount === true
          ? `${convertToChartValue(total)}`
          : `${total}`;
    label.fontSize = isMiniChart ? 16 : 20;
    label.isMeasured = false;
    label.y = isMiniChart ? -10 : 8;
    label.horizontalCenter = "middle";
    label.verticalCenter = "middle";

    let titleLabel = pieSeries.createChild(am4core.Label);
    titleLabel.text = totalTitle;
    titleLabel.horizontalCenter = "middle";
    titleLabel.verticalCenter = "middle";
    titleLabel.fontSize = isMiniChart ? 10 : 12;
    titleLabel.isMeasured = false;
    titleLabel.x = 0;
    titleLabel.y = -15;


    let growthLabel = pieSeries.createChild(am4core.Label);
    growthLabel.text = yearToYearValue && yearToYearValue > 5
      ? '+' + yearToYearValue + yearToYearUnit
      : yearToYearValue && yearToYearValue > 5 && '-' + yearToYearValue + yearToYearUnit;
    growthLabel.horizontalCenter = "middle";
    growthLabel.verticalCenter = "middle";
    growthLabel.fontSize = 10;
    growthLabel.isMeasured = false;
    growthLabel.x = 0;
    growthLabel.y = !titleLabel ? 18 : 7;
    growthLabel.fill = yearToYearValue >= 5
      ? am4core.color('#00b050')
      : am4core.color('#ff3333');

    let growthTextLabel = pieSeries.createChild(am4core.Label);
    growthTextLabel.text = yearToYearLabel;
    growthTextLabel.horizontalCenter = "middle";
    growthTextLabel.verticalCenter = "middle";
    growthTextLabel.fontSize = isMiniChart ? 10 : 12;
    growthTextLabel.isMeasured = false;
    growthTextLabel.x = 0;
    growthTextLabel.y = !titleLabel ? 30 : 20;


    chart.responsive.enabled = true;

    return () => {
      chart.dispose();
    };
  }, [data, id, isTotalPercent, isDollar, isDollarButWhole, legend, total, totalTitle, isAnimationEnabled, isCount, isMiniChart, yearToYearValue, yearToYearUnit, yearToYearLabel]);

  return (
    <div id={id} className="donut-chart" style={{ width: "100%", height: "100%" }}></div>
  );
}

export default DonutChart;