import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import NestedDonutChart from '../../components/chart/NestedDonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToTableValueUSD, convertToPercent } from '../../constants/formatter';


const OverviewContentType2 = (props) => {
	const { overviewData, selectedRangeValue, selected_group_by, tab_name, tab_id } = props;

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	const overall = overviewData ? overviewData.overall : [];
	const segment_details = overviewData ? overviewData.segment_details : [];
	const segment_trends_by_vertical = overviewData ? overviewData.segment_trends_by_vertical : [];

	// KPI Data Distribution
	const [kpiClicked, setkpiClicked] = useState(null);
	const customer_revenue_by_vertical = overviewData ? overviewData.customer_revenue_by_vertical : [];
	const customer_revenue_by_vertical_in_quarter_percentage = overviewData ? overviewData.customer_revenue_by_vertical_in_quarter_percentage : [];
	const customer_revenue_by_vertical_in_year_percentage = overviewData ? overviewData.customer_revenue_by_vertical_in_year_percentage : [];
	const customer_revenue_by_vertical_in_budget_percentage = overviewData ? overviewData.customer_revenue_by_vertical_in_budget_percentage : [];
	const customer_revenue_by_vertical_in_mpq_percentage = overviewData ? overviewData.customer_revenue_by_vertical_in_mpq_percentage : [];
	const nested_segment_revenue_mix = selectedRangeValue === "last_completed_quarter"
		? overviewData.nested_segment_revenue_mix_lcq
		: selectedRangeValue === "last_4_quarters"
			? overviewData.nested_segment_revenue_mix_l4q
			: overviewData.nested_segment_revenue_mix_ytd || [];

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToTableValueUSD(cell);
		return <span style={{ color: cell < 0 && '#ff3333' }}>{formatLongText(new_cell, row)}</span>
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};

	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		}, {
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		}, {
			dataField: 'rev_usd',
			text: 'MRR $',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		}, {
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right'
		}, {
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right'
		}, {
			dataField: 'q_to_q_growth_usd',
			text: 'Q-Q $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		}, {
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right'
		}, {
			dataField: 'y_to_y_growth_usd',
			text: 'Y-Y $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		}, {
			dataField: 'percent_mpq',
			text: '% MPQ',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right'
		}, {
			dataField: 'percent_budget',
			text: '% BUDGET',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right'
		}
	];

	return (
		<>
			<Row className="contentDivider pb-30">
				<div className="fix-width">
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{tab_name}</Card.Title>
							<KPI_NEW
								totalValue={overall.total.value}
								totalUnit={overall.total.unit}
								details={overall.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{tab_name} BY {selected_group_by}</Card.Title>
							{kpiClicked === '% of Budget'
								? <>
									<Legend legend={customer_revenue_by_vertical_in_budget_percentage.legend} />
									<div style={{ height: "400px" }}>
										<LineChart
											id={`customer_revenue_by_vertical_in_budget_percentage-line_chart-segment`}
											data={customer_revenue_by_vertical_in_budget_percentage.data}
											legend={customer_revenue_by_vertical_in_budget_percentage.legend}
											hideLabel={false}
											hideValueLabel={false}
											isCardSmaller={false}
											isPercentage={true}
											isOneHundredPercent={false}
											withLabel={true}
											withDottedLine={false}
											withGridLines={false}
											dasharrayValues={null}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								: kpiClicked === '% of MPQ'
									? <>
										<Legend legend={customer_revenue_by_vertical_in_mpq_percentage.legend} />
										<div style={{ height: "400px" }}>
											<LineChart
												id={`customer_revenue_by_vertical_in_mpq_percentage-line_chart-segment`}
												data={customer_revenue_by_vertical_in_mpq_percentage.data}
												legend={customer_revenue_by_vertical_in_mpq_percentage.legend}
												hideLabel={false}
												hideValueLabel={false}
												isCardSmaller={false}
												isPercentage={true}
												isOneHundredPercent={false}
												withLabel={true}
												withDottedLine={false}
												withGridLines={false}
												dasharrayValues={null}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									: kpiClicked === 'Q/Q Growth %'
										? <>
											<Legend legend={customer_revenue_by_vertical_in_quarter_percentage.legend} />
											<div style={{ height: "400px" }}>
												<LineChart
													id={`customer_revenue_by_vertical_in_quarter_percentage-line_chart-segment`}
													data={customer_revenue_by_vertical_in_quarter_percentage.data}
													legend={customer_revenue_by_vertical_in_quarter_percentage.legend}
													hideLabel={false}
													hideValueLabel={false}
													isCardSmaller={false}
													isPercentage={true}
													isOneHundredPercent={false}
													withLabel={true}
													withDottedLine={false}
													withGridLines={false}
													dasharrayValues={null}
													isAnimationEnabled={false}
												/>
											</div>
										</>
										: kpiClicked === 'Y/Y Growth %'
											? <>
												<Legend legend={customer_revenue_by_vertical_in_year_percentage.legend} />
												<div style={{ height: "400px" }}>
													<LineChart
														id={`customer_revenue_by_vertical_in_year_percentage-line_chart-segment`}
														data={customer_revenue_by_vertical_in_year_percentage.data}
														legend={customer_revenue_by_vertical_in_year_percentage.legend}
														hideLabel={false}
														hideValueLabel={false}
														isCardSmaller={false}
														isPercentage={true}
														isOneHundredPercent={false}
														withLabel={true}
														withDottedLine={false}
														withGridLines={false}
														dasharrayValues={null}
														isAnimationEnabled={false}
													/>
												</div>
											</>
											: <>
												<div className="d-flex">
													<Legend legend={customer_revenue_by_vertical.legend} />
													<BasicDD
														className="ml-auto"
														id="overview-stacked_column_dd"
														options={optionOverviewFilter}
														value={stackedColumnSelection}
														setValue={setStackedColumnSelection}
													/>
												</div>
												<div style={{ height: "400px" }}>
													<StackedColumnChart
														id={`${tab_id}-stacked_column_chart`}
														data={customer_revenue_by_vertical.data}
														legend={customer_revenue_by_vertical.legend}
														largerWidth={true}
														startValue={null}
														endValue={null}
														stackedColumnSelection={stackedColumnSelection.key}
														selectedRangeValue={"last_completed_quarter"}
														percentLabel={false}
														insideLabel={false}
														hiddenValueAxis={false}
														hideLabelBullet={false}
														isPercentage={false}
														isOneHundredPercent={false}
														isOverviewChart={true}
														isGrowthPercentToggable={true}												
														isAnimationEnabled={false}
														withNegativeValues={false}
														withMedianValue={false}
														medianValueName={null}
														disablePaddingTop={false}
														categoryAxisLabelDisabled={false}
														withUSDValues={true}
													/>
												</div>

											</>
							}
						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="Segment revenue">
						<Card.Body>
							<Card.Title>{tab_name} MIX % BY {selected_group_by}</Card.Title>
							<Legend legend={nested_segment_revenue_mix.legend} />
							<div style={{ height: "92%" }}>
								<NestedDonutChart
									id={`${tab_id}-nested-donut-chart`}
									insideValueName={"inner_quarter"}
									insideValue={"inner_quarter_percentage"}
									insideCategory={"hq_region"}
									outsideValueName={"outer_quarter"}
									outsideValue={"outer_quarter_percentage"}
									outsideCategory={"hq_region"}
									loadedData={nested_segment_revenue_mix.data}
									isAnimationEnabled={true}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{selected_group_by} DETAILS</Card.Title>
							<CustomTable
								tableName='segment-details scrollable'
								data={segment_details ? segment_details : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>Y/Y % GROWTH BY {selected_group_by}</Card.Title>
							<Legend legend={segment_trends_by_vertical.legend} />
							<div style={{ height: "300px" }}>
								<LineChart
									id={`${tab_id}-line_chart-segment`}
									data={segment_trends_by_vertical.data}
									legend={segment_trends_by_vertical.legend}
									hideLabel={false}
									hideValueLabel={false}
									isCardSmaller={true}
									isPercentage={true}
									isOneHundredPercent={false}
									withLabel={true}
									withDottedLine={false}
									withGridLines={false}
									dasharrayValues={null}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</>
	);
};

export default OverviewContentType2;