// Only uncomment this productOption when deploying for production (temporary)
const productOption = [
  {
    key: 'all_domains',
    text: 'All Domains'
  },
  {
    key: 'rev',
    text: 'Revenue'
  }
]

const AddMetricsModalMetro = (props) => {
	const { showAddMetricsModal, onCancelModal, selectedItemMetro, setSelectedItemMetro, stepList, setMetroOnApply } = props;
	const [checkedList, setCheckedList] = useState(stepList);

	// Basic DD
	const product_initial_value = productOption ? productOption[0] : null;
	const [productSelection, setProductSelection] = useState(product_initial_value);

	const onAddMetrics = () => {
		setMetroOnApply(true)
		onCancelModal()
	}

	const onCancel = () => {
		onCancelModal()
		setMetroOnApply(false)
	}

	const checkButton = (item) => {
		var newArray = selectedItemMetro.slice();
		var finalArray = [];
		var arr = stepList.map((val) => {
			if (val.isChecked === true && val.key === item.key) {
				val.isChecked = false;
				finalArray = selectedItemMetro.filter(data => data.key !== item.key)
				setSelectedItemMetro(finalArray)
			} else if (val.key === item.key) {
				val.isChecked = true;
				newArray.push(item);
				setSelectedItemMetro(newArray)
			}
			return val
		})

		setCheckedList(arr);
	}

	// Search Fuction
	const [searchVal, setSearchVal] = useState('');

	useEffect(() => {
		if (searchVal) {
			setProductSelection(product_initial_value)
		}
	}, [product_initial_value, searchVal])

	const onSelectBasicDD = (e) => {
		setSearchVal('')
	}

	return (
		<>
			<Modal
				show={showAddMetricsModal}
				onHide={onCancel}
				className="product-modal"
				backdrop="static"
				keyboard={false}
				centered
			>
				<div className="to-prevent-glitch">prevent glitch</div>
				<div className="content-wrapper">

					<AnimatePresence exitBeforeEnter>
						<ContentContainer
							modalTitle='CHOOSE ADD METRICS TO ADD'
							modalRight={
								<div className="filters">
									<BasicDD
										title="All Categories"
										id="all_categories"
										value={productSelection}
										setValue={setProductSelection}
										options={productOption}
										onSelect={onSelectBasicDD}
									/>
									<FormControl
										className="my-2 w-auto"
										placeholder={`Search...`}
										onChange={(e) => setSearchVal(e.target.value)}
										value={searchVal}
									/>
								</div>
							}
						>

							<Modal.Body>
								<Form className="product-form">
									{checkedList
										? <>
											<div className="product-container">
												{productSelection.key === 'all_domains'
													? <>
														{checkedList && checkedList.sort(function (a, b) {
															if (a.text < b.text) { return -1; }
															if (a.text > b.text) { return 1; }
															return 0;
														}).map((item) => {
															return (
																<div className="btn-container" key={item.key}>
																	<Button
																		variant="link"
																		id={item.key}
																		className={`checkbox-${item.isChecked}`}
																		onClick={() => checkButton(item)}
																	>
																		{item.icon}{item.text}
																	</Button>
																</div>
															)
														}).filter(
															(child) => !searchVal || child.props.children.props.children[1].toLowerCase().includes(searchVal.toLowerCase())
														)}
													</>
													: <>
														{checkedList && checkedList.sort(function (a, b) {
															if (a.text < b.text) { return -1; }
															if (a.text > b.text) { return 1; }
															return 0;
														}).map((item) => {
															return (
																<div className="btn-container" key={item.key}>
																	<Button
																		variant="link"
																		id={item.key}
																		className={`checkbox-${item.isChecked}`}
																		onClick={() => checkButton(item)}
																	>
																		{item.icon}{item.text}
																	</Button>
																</div>
															)
														}).filter(
															(child) => searchVal
																? !searchVal || child.props.children.props.children[1].toLowerCase().includes(searchVal.toLowerCase())
																: !productSelection.key || child.key.toLowerCase().split('_')[0] === productSelection.key
														)}
													</>
												}
											</div>
										</>
										: null
									}
								</Form>
							</Modal.Body>

							<Modal.Footer>
								<Button variant="light" onClick={() => onCancel()}>Cancel</Button>
								<Button variant="primary" type="submit" onClick={() => onAddMetrics()}> Next</Button>
							</Modal.Footer>
						</ContentContainer>
					</AnimatePresence>
				</div>
			</Modal>
		</>
	)
};

const ContentContainer = ({
	modalTitle,
	modalRight,
	children
}) => {
	return (
		<motion.div className={`content-container`}
			animate='visible'
		>
			{(modalTitle && modalRight) &&
				<Modal.Header>
					<Modal.Title>{modalTitle}</Modal.Title>
					{modalRight}
				</Modal.Header>
			}
			{children}
		</motion.div>
	)
}

export default AddMetricsModalMetro;